/**
 * Row
 */

.sc-flex {
  display: flex;
}

.sc-flex-row {
  display: flex;
  flex-direction: row;
}
@media only screen and (min-width: breakpoint('medium')) {
  .sc-md\:flex-row {
    display: flex;
    flex-direction: row;
  }
}
@media only screen and (min-width: breakpoint('large')) {
  .sc-lg\:flex-row {
    display: flex;
    flex-direction: row;
  }
}

@include small-and-up {
  .sc-flex-row-sm {
    display: flex;
    flex-direction: row;
  }
}

/**
 * Row
 */

.sc-flex-col {
  display: flex;
  flex-direction: column;
}
@media only screen and (min-width: breakpoint('medium')) {
  .sc-md\:flex-col {
    display: flex;
    flex-direction: column;
  }
}
@media only screen and (min-width: breakpoint('large')) {
  .sc-lg\:flex-col {
    display: flex;
    flex-direction: column;
  }
}

/**
 * Wrap
 */

.sc-flex-wrap {
  flex-wrap: wrap;
}

.sc-wrap-medium-and-up {
  @include medium-and-up {
    flex-wrap: wrap;
  }
}

.sc-wrap-up-to-medium {
  @include up-to-medium {
    flex-wrap: wrap;
  }
}

/**
 * Justify content
 */

.sc-justify-space-between {
  justify-content: space-between;
}

.sc-justify-space-evenly {
  justify-content: space-evenly;
}

.sc-justify-space-around {
  justify-content: space-around;
}

.sc-justify-center {
  justify-content: center;
}

.sc-justify-start {
  justify-content: flex-start;
}

.sc-justify-end {
  justify-content: flex-end;
}

.sc-even-distribution {
  display: flex;
  gap: var(--sc-spacing-base);
}

/**
 * Align items
 */

.sc-align-items-center {
  align-items: center;
}

.sc-align-items-start {
  align-items: flex-start;
}

.sc-align-items-end {
  align-items: flex-end;
}

/**
 * Grow & shrink
 */

.sc-grow {
  flex-grow: 1;
}

.sc-no-shrink {
  flex-shrink: 0;
}

